















import { Component, Emit, Vue } from 'vue-property-decorator';

@Component
export default class LBZ extends Vue {
  private vhours: number = new Date(Date.now()).getHours();
  private vtheme: string = 'light';

  @Emit()
  private _fsetTheme() {
    if (this.vhours >= 18 || this.vhours < 6) {
      this.vtheme = 'dark';
      document.querySelector('meta[name=apple-mobile-web-app-status-bar-style]')!.setAttribute('content', 'black');
      document.querySelector('meta[name=theme-color]')!.setAttribute('content', '#000');
    }
    document.documentElement.setAttribute('theme', this.vtheme);
  }

  private created() {
    this._fsetTheme();
  }
}
